import { useState, useEffect } from "react";
import * as d3 from "d3";

function Form(props) {
    const [formData, setFormData] = useState({
        region: { code: "37", name: " ."},
        income: "50000",
        period: "1",
        members: "1",
        status: 3
    });
    const [errors, setErrors] = useState({ income: "", members: "" });
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [codes, setCodes] = useState([]);

    useEffect(() => {
        const fetchCsv = async() => {
            try {
                const data = await d3.csv(process.env.PUBLIC_URL + '/codes.csv', (d) => {
                    return {
                        region: d.region,
                        name: d.name,
                        code: +d.code
                    }
                });
                setCodes(d3.sort(data, (d) => d.name));
            } catch (error) {
                console.log(error);
            }
        };
        fetchCsv();
    }, []);

    const validateNumeric = (name, value) => {
        if (value === "") {
            return " ";
        }
        if (!/^\d+$/.test(value)) {
            return "   ";
        }
        return "";
    };

    const handleChange = (evt) => {
        const { name, value, type } = evt.target;
        if (isSubmitting) {
            setIsSubmitting(false);
        }

        if (type === "select-one" && name === "region") {
            const selectedOption = evt.target.options[evt.target.selectedIndex];
            setFormData({ ...formData, [name]: {code: value, name: selectedOption.text} });
            return;
        }

        if (type === "radio") {
            setFormData({ ...formData, [name]: value });
            return;
        }

        if (name === "income" || name === "members") {
            const errorMsg = validateNumeric(name, value);
            setErrors({ ...errors, [name]: errorMsg });
            setFormData({ ...formData, [name]: value });
            return;
        }

        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = (evt) => {
        evt.preventDefault();
        const incomeError = validateNumeric("income", formData.income);
        const membersError = validateNumeric("members", formData.members);

        if (incomeError || membersError) {
            setErrors({ income: incomeError, members: membersError });
            return;
        }

        const payload = {
            ...formData,
            income: Number(formData.income),
            members: Number(formData.members)
        };

        props.onSubmit(payload);
        setIsSubmitting(true);
    };

    const hasErrors = !!errors.income || !!errors.members || formData.income === "" || formData.members === "";

    return (
        <div className="form">
            <h2 className="form__title">  </h2>
            <p className="form__text"> &nbsp; , &nbsp;    &nbsp;  &nbsp;   .</p>
            <p className="form__text">  &nbsp;&mdash;   ,       &nbsp;  &nbsp; . &nbsp;         ,  &nbsp;  .    &nbsp;  &nbsp;&mdash;  ,  &nbsp;&nbsp;&mdash;     .</p>
            <form className="form__body" onSubmit={handleSubmit} noValidate>
                <span className="form__label"> :</span>
                <select
                    className="form__input"
                    name="region"
                    value={formData.region.code}
                    onChange={handleChange}
                >
                    {codes.map((region, i) => (
                        <option key={i} value={region.code}>{region.name}</option>
                    ))}
                </select>
                <span className="form__label"> , :</span>
                <div className="form__group">
                    <div className="form__line">
                        <input
                            className="form__input income"
                            name="income"
                        type="number"
                            value={formData.income}
                            onChange={handleChange}
                            min="0"
                            max="10000000000"
                            step="50000"
                        />
                    {/*{errors.income && <p className="form__caption form__caption-warning">{errors.income}</p>}*/}
                        <div className="form__toggle">
                            <label className="form__radio">
                                <input 
                                    className="form__radio-input"
                                    id="month"
                                    name="period"
                                    type="radio"
                                    value="1"
                                    onChange={handleChange}
                                    checked={formData.period === "1"}
                                />
                                <span className="form__radio-label" aria-hidden="true"> </span>
                            </label>
                            <label className="form__radio">
                                <input 
                                    className="form__radio-input"
                                    id="year"
                                    name="period"
                                    type="radio"
                                    value="12"
                                    onChange={handleChange}
                                />
                                <span className="form__radio-label" aria-hidden="true"> </span>
                            </label>
                        </div>
                    </div>
                    <p className="form__caption">      .  , , , , ,  &nbsp; .</p>
                </div>
                <span className="form__label"> :</span>
                <div className="form__group">
                    <input
                        className="form__input members"
                        name="members"
                        type="number"
                        value={formData.members}
                        onChange={handleChange}
                        min="1"
                        max="100"
                        step="1"
                    />
                    {/*{errors.members && <p className="form__caption form__caption-warning">{errors.members}</p>}*/}
                    <p className="form__caption"> ,  ,  , &nbsp;  &nbsp;23&nbsp;,  &nbsp;  , &nbsp;,   .</p>
                </div>
                <span className="form__label">  :</span>
                <select
                    className="form__input stretched"
                    name="status"
                    value={formData.status}
                    onChange={handleChange}
                >
                    <option value="1">     </option>
                    <option value="2">   </option>
                    <option value="3">     </option>
                    <option value="4">  ,    </option>
                    <option value="5">   ,  </option>
                </select>
                <span className="form__label"></span>
                <button className="form__button" type="submit" onSubmit={handleSubmit} disabled={isSubmitting || hasErrors}></button>
            </form>
        </div>
    )
}

export default Form;